import React, { Component } from 'react';
import Modal from 'react-responsive-modal';


export default class Work extends Component {

  constructor(props){
    super(props);
    this.state = {
      open: false,
      itemModal: {}
    }
  }

 onOpenModal = () => {
    this.setState({open: true});
    // 
  }

  onCloseModal = () => {
    this.setState({ open: false });
  };

  perfect = () =>{
      console.log(this.props.data.name);
  }  


  render(){
      return(
        <div className="twelve columns">            
             <h1 onClick={this.perfect} >{this.props.ofIndex} </h1>
             <img  src={`${this.props.data.imgurl}`} alt={this.props.data.name}  className="img-work" onClick={() => this.onOpenModal()} />
             <div className="portfolio-item-meta">
                <h5>{this.props.data.name} </h5>
                <p>{this.props.data.description}</p>
             </div>
          
            <Modal classNames="modal-work" open={this.state.open} onClose={this.onCloseModal.bind()} >
            <div className="row">
                <div className="six columns">
                <img  src={`${this.props.data.imgurl}`} alt={this.props.data.name} />
                </div>
                    <div className="six columns">
                    <h1 className="detail-plan-nombre">{this.props.data.name}</h1>
                    <p className="detail-plan-item"><b>Description: </b><span>{this.props.data.description}</span></p>
                    <p className="detail-plan-item"><b>Technology: </b><span>{this.props.data.description}</span></p>              
                </div>
            </div>
        </Modal>
      </div>
      )
  }




}


