import React, { Component } from 'react';
import Typing from 'react-typing-animation';

export default class Header extends Component { 

  render() {
    let resumeData = this.props.resumeData;
    return (
      <React.Fragment>
      {/*generated code*/}
      <header id="home">
         <nav id="nav-wrap">
            <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
          <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
            <ul id="nav" className="nav">
               <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
               <li><a className="smoothscroll" href="#about">About</a></li>
             <li><a className="smoothscroll" href="#resume">Resume</a></li>
               <li><a className="smoothscroll" href="#portfolio">Works</a></li>
               <li><a className="smoothscroll" href="#testimonials">Testimonials</a></li>
               <li><a className="smoothscroll" href="#contact">Contact</a></li>
            </ul>
         </nav>

         <div className="row banner">
            <div className="logo-banner">
              <img className="animate-spin"  src="../../../images/ctolenk-gray.png"  alt="ctolenk" />
            </div>
            <div className="banner-text">
               {/* <h1 className="responsive-headline">I am {resumeData.name}.</h1> */}
               {/* <h1 className="responsive-headline">I am {resumeData.name}.</h1> */}
              <Typing loop={true} speed={95} cursorClassName='cursos-typing'> 
                <h1 className="responsive-headline">I am Carlos Tolentino    </h1> 
                <Typing.Backspace count={20}/>
                <h1 className="responsive-headline">Web Developer </h1>       
                <Typing.Backspace count={14}/>
                <h1 className="responsive-headline">Mobile Developer </h1>      
                <Typing.Backspace count={17}/>
                <h1 className="responsive-headline">Frontend Developer </h1>   
                <Typing.Backspace count={19}/>
                <h1 className="responsive-headline">Backend Developer</h1>  
                <Typing.Reset count={0} delay={500} />      
                
              </Typing>    
               <h3>I am a {resumeData.role}. {resumeData.roleDescription}
               </h3>
               <hr/>
               <ul className="social">
                  {
                    resumeData.socialLinks && resumeData.socialLinks.map(item =>{
                      return(
                              <li key={item.name}>
                                <a href={item.url} target="_blank"><i className={item.className}></i></a>
                              </li>
                            )
                          }
                    )
                  }
               </ul>
            </div>
         </div>

         <p className="scrolldown">
            <a className="smoothscroll" href="#about"><i className="icon-down-circle"></i></a>
         </p>

      </header>
      </React.Fragment>
    );
  }
}





