import React, { Component } from 'react';
import Slider from "react-slick";


//Component
import Work from './work';


export default class Porfolio extends Component {
  constructor(props){
    super(props);
    this.state = {
      open: false,
      itemModal: {}
    }

  }

 

  render() {
    const settings = {    
      dots: true,    
      fade: true,
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 3000,     
    };

    let resumeData = this.props.resumeData;    
    return (
        <section id="portfolio" className="portfolioContent">
          <article className="row work">
      
          <div className="twelve columns main-col">
            <h1>Check Out Some of My Works.</h1>

              <Slider className="slider-carousel">
              {
               resumeData.portfolio.map((item, index) =>{                     
                  return(               
                     <Work key={index} data={item}  />
                  );
                }, this)
              }
              </Slider>
            </div>
         </article>
      </section>
    );
  }
}



